<template>
    <div>
        <HeaderTicket :name="client.first_name" :handle-open="openModal" />
        <Modal :is-open-modal="isOpenModal" :close-modal="closeModal" />

        <b-container v-show="!isMobile">
            <h2 class="text-left  d-flex align-items-center buy-trip__title" 
                v-if="ticketsbuy.length"
            >
                {{ ticketsbuy[0].detail.route.from.settlement }} - {{ticketsbuy[0].detail.route.to.settlement}}</h2>
        </b-container>

        <b-container class="mb-1" v-show="!isMobile">
            <RowFind/>
        </b-container>

        <b-container v-if="!isMobile">
            <div class="tablo-title text-left d-flex align-items-center px-2 "
            v-if="ticketsbuy.length">
                Квитки {{ ticketsbuy[0].detail.route.from.settlement }} -
                {{ticketsbuy[0].detail.route.to.settlement}} на
                <span class="tablo-title__date">{{ticketsbuy[0].detail.route.from.date}}</span>
            </div>
        </b-container>

        <b-container v-else class="mb-2">
            <div class="tablo-title text-left  d-flex align-items-center justify-content-between px-3 justify-content-between"
            v-if="ticketsbuy.length">
                <span>{{ ticketsbuy[0].detail.route.from.settlement }} &#8594;
                {{ticketsbuy[0].detail.route.to.settlement}} </span>
                <span  class="tablo-title__date">&nbsp;{{ticketsbuy[0].detail.route.from.date}}</span>
            </div>
        </b-container>

        

        <b-container>
            <Table/>
        </b-container>

        <p class="text-center pt-4 title-center-info">
            Щоб отримати більше інформації про перевезення та замовити квиток
        </p>
        <button class="btn-do mb-5 text-uppercase btn-become">Стати своїм</button>

        <Footer/>


    </div>
</template>

<script>
    import HeaderTicket from "./HeaderTicket";
    import Table from "./Table";
    import RowFind from "./RowFind";
    import Footer from "./Footer";
    import Modal from "./Modal";
    import {mapActions, mapState} from "vuex";

    export default {
        name: "BuyTrip",
        data() {
            return {
                //city1, city2, date
                cityFROM: '',
                cityTO: '',
                dataSettlement: '',

                //other
                client: {},
                cityArray: [],
                selectedCity: {
                    city: "Вінниця",
                    place_id: 'ChIJiWRaGWVbLUcR_nTd7lnh1Ms'
                },
                isOpenModal: false,
                isMobile: false,
            }
        },
        computed: {
            ...mapState({
                token: state => state.token,
                isAuth: state => state.isAuth,
                ticketsbuy: state => state.ticketsbuy,
                // objbuy: state => state.objbuy
            })
        },
        methods: {
            ...mapActions(['AUTH_LOGOUT']),
            LOGOUT() {
                this.$store.dispatch('AUTH_LOGOUT')
                    .then(() => {
                        this.$router.push({ path: 'home' })
                    })
            },
            openModal() {
                this.isOpenModal = true;
            },
            closeModal() {
                this.isOpenModal = false;
            },
        },
        components: {
            HeaderTicket, Table, RowFind, Footer, Modal
        },
        created() {
            if (window.innerWidth > 576) {
                this.isMobile = false
            } else {
                this.isMobile = true
            }

            //GET CLIENT INFORMATION
            const headers = {
                'Content-type': 'application/json',
                'Authorization': `Bearer ${this.token}`, //передача токена
            }
            axios.get('web/user', {
                headers: headers
            })
                .then((response) => {
                    this.client = response.data.result
                })
                .catch(e => {
                    if (e.response.status === 401) {
                        console.log('Не авторизований')
                    }
                })

            // console.log(this.objbuy)

        }
    }
</script>

